$breakpoint1: 500px;

.content .dataframe-selection {
    padding: 10px 20px 0 20px;
    height: auto;

    @media (max-width: $breakpoint1) {
        padding: 5px;
        height: calc(100vh - 194px);
        margin: 0;
    }

    h2 {
        font-size: 18px;
        color: #333;
    }

    &.table-container :not(.ant-table-empty) .ant-table-body {
        height: calc(100vh - 214px);
        max-height: calc(100vh - 214px);

        @media (max-width: $breakpoint1) {
            min-width: 400px;
            height: calc(100vh - 250px);
            max-height: calc(100vh - 250px);
        }

        .dataframe-rows,
        .dataframe-columns {
            width: 120px;
            text-align: center;
        }
    }

    &.table-container .ant-table-empty .ant-table-body {
        display: none;
    }

    &.table-container .ant-table-empty .ant-table-placeholder {
        border: none;
    }
}

.content .directory-selection {
    .saveas-container {
        height: 70px;
        background: #EEF1F3;
        margin-top: -10px;
        border-bottom: 2px solid #fff;
        text-align: center;
        line-height: 70px;

        label {
            font-size: 13px;
            color: #555;
            margin-right: 26px;
        }

        input {
            width: 315px;
            height: 25px;
            padding: 6px 0 5px 11px;
            border: 1px solid #E5E5E5;
            font-size: 12px;
            color: #555;

            @media (max-width: $breakpoint1) {
                width: 60%;
            }

            &.invalid {
                border: 1px solid crimson;
            }

            &:focus {
                outline: none;
            }
        }
    }

    .folder-tree-container {
        width: 342px;
    }

    &.table-container .ant-table-body {
        height: calc(100vh - 283px);
        max-height: calc(100vh - 283px);
    }

    .directory-selection-content {
        .left-panel {
            height: calc(100vh - 228px) !important;
            margin-top: 10px;
            width: 324px;
            border: none;

            @media (max-width: $breakpoint1) {
                height: calc(100vh - 259px) !important;
            }
        }
    }
}

#new-folder-name-input {
    width: 400px;
    border: 1px solid #E5E5E5;
    padding: 5px;

    &:focus {
        outline: none;
    }

    &.invalid {
        border: 1px solid crimson;
    }
}