$initials-circle-size: 30px;
$user-tooltip-hide-breakpoint: 270px;
$tooltip-content-font-color: #3F4042;

.ant-popover.user-tooltip-overlay {
    width: 230px;
    position: relative;

    @media (max-width: $user-tooltip-hide-breakpoint) {
        display: none;
    }

    .ant-popover-content {
        position: relative;
        top: 7px;
        left: 25px;
    }

    .ant-popover-inner-content {
        background: #fff;
        color: $tooltip-content-font-color;
        padding: 0;
        text-align: left;
    }

    .user-info {
        display: flex;
        padding: 10px;
        border-bottom: 1px solid #E8EDF0;

        .circle {
            color: #fff;
            background: #b6b6b6;
            width: $initials-circle-size;
            min-width: $initials-circle-size;
            height: $initials-circle-size;
            border-radius: 50%;
            text-align: center;
            font-family: sans-serif;
            position: relative;
            top: 5px;

            .initials {
                font-size: calc(#{$initials-circle-size} / 2);
                font-weight: 300;
                line-height: 1;
                position: relative;
                top: calc(#{$initials-circle-size} / 4);
            }
        }

        .name-url {
            padding-left: 10px;

            h3 {
                font-size: 12px;
                margin: 0;
            }

            p {
                color: #3F4042;
                font-size: 10px;
                width: 150px;
                margin: 0;
                word-break: break-all;
            }
        }
    }

    .links {
        ul {
            list-style: none;
            padding: 0;
            margin: 0;
        }

        li {
            a, button {
                color: $tooltip-content-font-color;
                font-size: 12px;
                display: block;
                padding: 8px 15px;
                background: transparent;
                border: none;
                width: 100%;
                text-align: left;
                cursor: pointer;

                &:hover {
                    background: #F0F7FE;
                }

                &:focus {
                    background: #F0F7FE;
                    outline: 3px solid #1890ff;
                    outline-offset: 3px;
                    box-shadow: none;
                }
            }
        }

        .version-number {
            font-weight: 300;
            text-align: center;
            color: #1C91DC;
            padding: 10px;
        }
    }
}