/* Authentication */
.main-content.authentication {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-top: 50px !important;
}

.mstr-logo-authentication {
  width: 70px;
  height: 70px;
}

.authentication h1 {
  font-size: 20px;
  margin-top: 16px;
  margin-bottom: 35px;
}

.authentication h2 {
  font-size: 14px;
  margin-bottom: 33px;
}

.authentication-form {
  width: 303px;
  margin: 0 auto;
  padding-bottom: 5px;
}

.authentication-form--hidden {
  display: none;
}

.authentication-form .ant-form-item-control-wrapper {
  width: 100%;
}

.authentication-form input {
  border: none;
  border-bottom: 1px solid #e2e2e2;
  border-radius: 0;
  padding-left: 0;
  padding-right: 0;
}

.authentication-form input:focus,
.authentication-form input:hover {
  border: none;
  border-bottom: 1px solid #444649;
  border-radius: 0;
}

.authentication .authentication-form .ant-select-search__field {
  border-radius: 0;
}

.ant-select-selection {
  box-shadow: none;
}

#root .content .authentication .authentication-form div,
#root .content .authentication .authentication-form input {
  box-shadow: none;
  font-family: "Roboto";
  font-size: 14px;
  text-overflow: ellipsis;
}

.ant-form-item-label label {
  font-size: 12px;
  color: #333333;
  opacity: 0.5;
}

.authentication
