}

.authentication-form-credentials > .ant-form-item:first-child {
  margin-bottom: 0;
}

.authentication-form-credentials input[placeholder="Username"] {
  margin-top: 5px;
}

.authentication-form-credentials input.invalid,
#root .content #authForm .has-error .ant-form-explain,
.incorrect-credentials-info p {
  color: rgba(217, 35, 46, 1);
  font-size: 12px;
}

#root .content #authForm .has-success .ant-form-explain {
  display: none;
}

