  font-size: 12px;
  line-height: 17px;
}

.authentication-form .ant-btn.ant-btn-primary {
  border-radius: 8px;
}

.red-info svg {
  width: 17px;
  height: 17px;
  margin-right: 5px;
}

.incorrect-credentials-info {
  height: 35px;
  -webkit-transition: height 0.2s linear;
  transition: height 0.2s linear;
}

.incorrect-credentials-info.invisible {
  height: 0;
  -webkit-transition: height 0.2s linear;
  transition: height 0.2s linear;
}

/* Popover */
.recent-environments {
  width: 350px;
}

.recent-environments ul {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  max-height: 132px;
  overflow: auto;
  border-top: 1px solid rgba(51, 51, 51, 0.05);
  border-bottom: 1px solid rgba(51, 51, 51, 0.05);
}

.recent-environments ul li {
  padding: 7px 5px 7px 20px;
  display: flex;
  text-align: left;
}

.recent-environments li:hover,
.recent-environments li.selected {
  background-color: rgba(51, 51, 51, 0.05);
}

.recent-environments ul li div:first-child {
  overflow: hidden;
  font-size: 12px;
  color: rgba(51, 51, 51, 0.5);
