.recent-environments .recent-env-button p {
  color: #919191;
  color: rgba(51, 51, 51, 0.5);

}

.recent-environments ul li.selected p,
.recent-environments .recent-env-button:hover p {
  color: #000;
}

.recent-environments .remove-env-button {
  font-size: 14px;
  line-height: 51px;
  color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  padding-left: 5px;
}

.recent-environments .remove-env-button i:hover {
  color: #000;
}

.recent-environments h3 {
  font-size: 14px;
  margin: 0;
}

body .ant-popover.recent-environments .ant-popover-inner-content {
  background-color: #fff;
  padding: 0;
}

body .ant-popover-arrow {
  display: none;
}

.recent-environments--hidden {
  display: none;
}

.recent-environments .clear-recent {
  font-size: 12px;
  padding: 5px 5px 5px 20px;
  text-align: left;
}

.recent-environments button {
  border: none;
  background: none;
  padding: 0;
}

.recent-environments .clear-recent button {
  color: #1890ff;
  cursor: pointer;
}

.recent-environments .recent-environment-list button {
  text-align: left;
  cursor: pointer;
}

.recent-environments .ant-popover-title {
  color: #919191;
  border: none;
  padding: 10px 5px 10px 20px;
}

.ant-popover .ant-popover-content {
  font-family: "Roboto", Arial, Verdana, Helvetica, sans-serif;
}

/* Confirmation modal */
.confirm-wrapper {
  display: flex;
  font-family: "Roboto", Arial, Verdana, Helvetica, sans-serif;
}

.confirm-info {
  padding-left: 20px;
  line-height: 35px;
}

.confirm-info p {
  margin-bottom: 5px;
}

.confirm-icon i {
  font-size: 35px;
  color: rgba(217, 35, 46, 1);
}

.confirm-title {
  font-size: 16px;
  font-weight: 900;
}

.confirm-modal .ant-modal-body {
  padding-bottom: 0;
}

/* Radio buttons */
.authentication .ant-radio {
  vertical-align: middle;
  display: none;
}

.authentication .ant-radio-group:focus {
  outline: 3px solid #1890ff;
  outline-offset: 3px;
  box-shadow: none;

}

.authentication .ant-radio-inner {
  border-color: #cecece;
  width: 13px;
  height: 13px;
}

.authentication #loginMode .ant-radio-wrapper {
  line-height: 16px;
}

.authentication #loginMode .ant-radio-wrapper:first-child {
  margin-right: 70px;
}

.authentication .ant-radio-wrapper-checked {
  color: #2094DD;
  border-bottom: 2px solid #2094DD;

}

.authentication .ant-radio-wrapper span {
  padding: 0 0;
}

.authentication .ant-radio-inner:after {
  background-color: #444649;
  width: 7px;
  height: 7px;
  top: 2px;
  left: 2px;
}

.authentication .ant-radio-wrapper:hover .ant-radio .ant-radio-inner, 
.authentication .ant-radio:hover .ant-radio-inner, 
.authentication .ant-radio-focused .ant-radio-inner
.authentication .ant-radio-checked:after,
.authentication .ant-radio-checked .ant-radio-inner {
  border: 1px solid #444649;
}

/* Connect button */
#connectButton {
  width: 303px;
  height: 42px;
}

#connectButton:hover {
  border-radius: 8px;
}

#connectButton.invalid {
  top: 0;
}
