/* Checkboxes in the wrangle data pane */
.checkbox {
    cursor: pointer;
    display: flex;
}

.checkbox input[type='checkbox'] {
    height: 0;
    width: 0;
    margin-right: 23px;
}

.checkbox input[type='checkbox']:before {
    content: "";
    height: 14px;
    width: 14px;
    border: 1px solid #d9d9d9;
    background: #fff;
    border-radius: 2px;
    cursor: pointer;
    display: inline-block;
}

.checkbox input[type='checkbox']:focus,
.checkbox input[type='checkbox'][tabfocused="1"] {
    outline: none !important;
}

.checkbox input[type='checkbox'][tabfocused="1"]:before {
    outline: 3px solid #1890ff;
    outline-offset: 2px;
    box-shadow: none;
    border-color: #1890ff;
}

.checkbox input[type='checkbox']:checked:before {
    background-color: #1890ff;
    border-color: #1890ff;
}

.checkbox:hover input[type='checkbox']:before {
    border-color: #1890ff;
}

.checkbox input[type='checkbox']:after {
    width: 6px;
    height: 9px;
    border: 1px solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: translate(4px, -16px) rotate(45deg);
            transform: translate(4px, -16px) rotate(45deg);
    content: "";
    position: relative;
    display: none;
}

.checkbox input[type='checkbox']:checked:after {
    display: block;
}

.checkbox span::-moz-selection {
    background: transparent;
    color: inherit;
}

.checkbox span::selection {
    background: transparent;
    color: inherit;
}

