}

.ant-table-body {
    overflow-x: auto !important;
}

.modal-preview .ant-table-body {
    overflow-x: visible !important;
}

.modal-preview .ant-table-body .attribute-column {
    text-align: left;
}

.modal-preview .ant-table-content > .ant-table-scroll > .ant-table-body {
    margin: 0;
}

.modal-preview .ant-table-body .metric-column {
    text-align: right;
}

#mstr-table.ant-table-small > .ant-table-content > .ant-table-body > table {
    padding: 0;
}

#mstr-table .ant-table-thead > tr > th {
    background-color: #ededed;
    overflow: hidden;
    white-space: nowrap;
}

.preview-note {
    padding: 5px 0;
    font-size: 11px;
    text-align: right;
}

/* Icons */
.mstr-icon svg {
    max-width: 18px;
    max-height: 18px;
    vertical-align: text-top;
}

.mstr-icon.selector svg {
    max-height: 14px;
    vertical-align: text-bottom;
    margin-right: 5px;
}

.mstr-icon.dataset svg {
    max-width: 14px;
    vertical-align: text-bottom;
}

.mstr-icon.certified svg {
    margin-left: 0;
    vertical-align: text-bottom;
}

.authentication .mstr-logo svg {
    width: 70px;
    height: 70px;
    max-width: unset;
    max-height: unset;
}

/* Tooltip */
.info-tooltip {
    z-index: 13;
}

.info-tooltip.one-liner {
    max-width: unset;
}

.ant-tooltip-inner {
    font-size: 12px;
    min-height: unset;
    padding: 3px 5px;
    background: #000;
}

.ant-tooltip-placement-top .ant-tooltip-arrow,
.ant-tooltip-placement-topLeft .ant-tooltip-arrow,
.ant-tooltip-placement-topRight .ant-tooltip-arrow {
    border-top-color: #000;
}

.ant-tooltip-placement-bottom .ant-tooltip-arrow,
.ant-tooltip-placement-bottomLeft .ant-tooltip-arrow,
.ant-tooltip-placement-bottomRight .ant-tooltip-arrow {
    border-bottom-color: #000;
}

.ant-tooltip-placement-right .ant-tooltip-arrow,
.ant-tooltip-placement-rightTop .ant-tooltip-arrow,
.ant-tooltip-placement-rightBottom .ant-tooltip-arrow {
    border-right-color: #000;
}

.ant-tooltip-placement-left .ant-tooltip-arrow,
.ant-tooltip-placement-leftTop .ant-tooltip-arrow,
.ant-tooltip-placement-leftBottom .ant-tooltip-arrow {
    border-left-color: #000;
}

.ant-tooltip.no-arrow .ant-tooltip-arrow {
    display: none;
}

/* Version tag */
#version {
    display: inline-block;
    font-size: 12px;
    padding-left: 10px;
    vertical-align: text-bottom;
    height: 25px;
    color: rgba(0, 0, 0, 0.25);
}

/* Tooltip */
.rc-tooltip-inner {
    padding: 20px 20px;
    color: #444649;
    text-align: left;
    text-decoration: none;
    background-color: #ffffff;
    border-radius: 6px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    min-height: 34px;
  border: solid #bebfc0 1px;
    border-radius: 4px;
    box-shadow: 0 0 8px 0 #827d7d;
}

.rc-tooltip-placement-topLeft .rc-tooltip-arrow {
    left: 35px;
    bottom: 10px;
    margin-left: -5px;
    border-width: 0 0 0;
    border-top-color: transparent;
}

.rc-tooltip-arrow:after,
.rc-tooltip-arrow:before {
	top: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.rc-tooltip-arrow:after {
	border-color: rgba(255, 255, 255, 0);
	border-top-color: #ffffff;
	border-width: 6px;
	margin-left: -6px;
}
.rc-tooltip-arrow:before {
	border-color: rgba(82, 82, 82, 0);
  border-top-color: #bebfc0;
	border-width: 9px;
    margin-left: -9px;
}

.tooltip-header {
    display: inline-block;
    text-align: right;
  color: #a2a2a4;
    padding-right: 15px;
}

.tooltip-description {
    display: inline-block;
}

.rc-tooltip {
    opacity: 1;
}

.tooltip-first-line {
    margin-bottom: 7px;
}

/* filters */
.filter-title-container {
    display: flex;
    min-width: 257px;
}

.filter-title {
    overflow: hidden;
    text-overflow: ellipsis;
    flex-grow: 1;
    line-height: 21px;
}

.ant-tree-title {
    display: inline-block;
    width: 100%;
}

.filter-list {
    overflow-x: hidden;
}
.rstudio-connector-params label {
    font-size: 13px;
  color: rgba(0, 0, 0, 0.65);
}

/* FolderBrowser */
.content .table-container.object-browser {
    margin: 0;
    overflow: hidden;
}

.content .ant-table-fixed-header .ant-table-scroll .ant-table-header {
    overflow: auto;
    z-index: 12;
    margin-bottom: 0;
    padding-bottom: 0;
    position: relative;
    padding: 0 15px;
    background: #fff;
    border-bottom: 1px solid #e8e8e8
}

.ant-table-header .ant-table-fixed {
    min-width: calc(100% - 5px);
}

.content[mode="browsing"] .ant-table-thead > tr > th {
    border-bottom: none;
}

#connectButton.ml-auto {
  float: left;
}

/* remove doubled search icon */
#search-toolbar > div > span > span > i.anticon-search:before {
    content: none;
}

.content .ant-row.full-height,
.context .ant-row .full-height {
  height: calc(100vh - 159px);
  width: 100vw;
}

.footer button {
    margin-left: 8px;
}

.footer > button {
    margin: 0;
}

.content .smart-folders-empty-table .ant-table-placeholder {
    border: 0;
}

.ant-modal-content .ant-table-wrapper {
    max-height: calc(100vh - 300px);
    overflow: auto;
}

.ant-modal {
    position: relative;
    top: 60px;
}
.ant-modal-close-icon:before {
    content: none;
}

.ant-btn {
    min-width: 90px;
}

.flex-container {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

div .ant-tooltip.info-tooltip .ant-tooltip-inner {
    text-align: center;
}

div .ant-tooltip.hidden {
    display: none;
}

.center-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.content .folder-browser-title {
    max-width: calc(100vw - 313px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: none;
    height: auto;
    padding: 11px 20px 11px 12px;
}

@media (max-width: 400px) {
    .content .folder-browser-title {
        font-size: 0;
        text-indent: -1000em;
    }
}

.content .filter-panel-container {
    border-top: 1px solid #E8EDF0;
}



/* Data preview */
.modal-preview .ant-table-content > .ant-table-scroll > .ant-table-body {
    overflow-y: visible;
    overflow-y: initial;
}

.modal-preview .ant-table-scroll {
    overflow: unset;
    overflow-x: visible;
}

.modal-preview .ant-table-small.ant-table-bordered {
    border: none;
}

.modal-preview .ant-table-small.ant-table-bordered .ant-table-content {
    border: none;
}

.modal-preview .ant-table-fixed {
    border: 1px solid #e8e8e8 !important;
}

.modal-preview #mstr-table .ant-table-thead>tr>th {
    font-size: 15px;
}

.modal-preview #mstr-table .ant-table-tbody>tr>td {
    font-size: 14px;
}

.modal-preview .ant-radio-button-wrapper:hover {
    color: #000;
    color: initial;
    background: #F3F3F3;
}

.modal-preview tbody .metric-column,
.modal-preview tbody .attribute-column {
    max-width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}
/* Remove animations/glows from buttons */
.content .ant-btn:focus {
    outline: 3px solid #1890ff;
    outline-offset: 3px;
    box-shadow: none;
    -webkit-transition: none;
    transition: none;
}

.content .ant-btn:after {
    display: none;
}

.ant-checkbox {
    -webkit-animation: none;
            animation: none;
}

.ant-tree-checkbox-checked:after,
.ant-checkbox-checked:after,
.ant-radio-checked:after {
    -webkit-animation: none;
            animation: none;
    border: none;
    -webkit-transition: none;
    transition: none;
}

.ant-checkbox-inner,
.ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-tree-checkbox-inner:after,
.ant-tree-checkbox-checked .ant-tree-checkbox-inner:after,
.ant-checkbox-checked .ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-radio-checked .ant-radio-inner:after,
.ant-radio-inner,
.ant-radio-inner:after {
    -webkit-transition: none;
    transition: none;
}

.ant-radio-input:focus + .ant-radio-inner {
    box-shadow: none;
}

input[tabfocused="1"] + .ant-radio-inner,
input[tabfocused="1"] + .ant-checkbox-inner {
    outline: 3px solid #1890ff;
    outline-offset: 1px;
    box-shadow: none;
}

/* Search input */
.content .ant-input-affix-wrapper .ant-input:not(:last-child) {
    padding-left: 30px;
    padding-right: 7px;
}

.content .ant-input-affix-wrapper .ant-input-suffix {
    left: 12px;
    right: 0;
    width: 16px;
}

/* Loading modal */
.loading-modal-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading-modal-spin {
    padding-right: 20px;
}

.loading-modal-text p {
    margin: 0;
}

/* Checkboxes in the data preparation pane */
.checkbox-list label.checkbox {
    cursor: pointer;
}

.checkbox-list input[type='checkbox'] {
    position: absolute;
    cursor: pointer;
    height: 0;
    width: 0;
    top: 4px;
}

.checkbox-list span.mstr-icon {
    padding-left: 17px;
}

.checkbox-list .ant-empty-image span.mstr-icon {
    padding: 0;
}

.checkbox-list .checkbox input[type='checkbox']:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 14px;
    width: 14px;
    border: 1px solid #d9d9d9;
    background: #fff;
    border-radius: 2px;
    cursor: pointer;
}

.checkbox-list input[type='checkbox']:focus,
.checkbox-list input[type='checkbox'][tabfocused="1"] {
    outline: none !important;
}

.checkbox-list input[type='checkbox'][tabfocused="1"]:before {
    outline: 3px solid #1890ff;
    outline-offset: 2px;
    box-shadow: none;
    border-color: #1890ff;
}

.checkbox-list .checkbox input[type='checkbox']:checked:before {
    background-color: #1890ff;
    border-color: #1890ff;
}

.checkbox-list .checkbox:hover input[type='checkbox']:before {
    border-color: #1890ff;
}

.checkbox-list input[type='checkbox']:after {
    left: 4px;
    top: 2px;
    width: 6px;
    height: 9px;
    border: 1px solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    content: "";
    position: absolute;
    display: none;
}

.checkbox-list input[type='checkbox']:checked:after {
    display: block;
}

label.checkbox span:last-of-type {
    overflow: hidden;
    text-overflow: ellipsis;
}

.checkbox span::-moz-selection {
    background: transparent;
    color: inherit;
}

.checkbox span::selection {
    background: transparent;
    color: inherit;
}

button:focus {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    -webkit-transition: none;
    transition: none;
}

button:focus:not(.focus-visible) {
    outline: none !important;
    box-shadow: none !important;
    -webkit-transition: none !important;
    transition: none !important;
}

[tabfocused="1"] {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    -webkit-transition: none;
    transition: none;
}

.ant-btn:focus:not(.some) {
    box-shadow: none;
}

.whole-page-loading {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

input[type="checkbox"].focus-visible {
    box-shadow: none !important;
}

