$breakpoint1: 500px;
$user-tooltip-hide-breakpoint: 270px;
$focus-outline: #1890ff;

.top-bar {
    box-shadow: 0 0 10px 1px #ccc;
    margin-bottom: 10px;
    position: relative;
    z-index: 13;
    background: #fff;
    display: flex;
    justify-content: space-between;

    @media (max-width: $breakpoint1) {
        display: block;
        padding-bottom: 5px;
    }


    .top-nav {
        height: 58px;
        color: #333333;
        font-weight: 500;
        font-size: 18px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 43px;
        padding: 0 20px 0 30px;

        @media (max-width: $breakpoint1) {
            // display: flex;
            height: unset;
            padding: 0;
            line-height: unset;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            margin-top: 15px;

            @media (max-width: $breakpoint1) {
                margin-top: 0;
                display: flex;
                justify-content: space-around;
                margin: 4px 4px 0 4px;
            }

            li {
                display: inline-block;

                &:first-child {
                    margin-right: 30px;
                }

                @media(max-width: $breakpoint1) {
                    width: 50%;
                }

                button {
                    font-size: 14px;
                    color: #888;
                    background: none;
                    padding: 10px 5px;
                    margin-right: 10px;
                    cursor: pointer;
                    border: none;
                    line-height: 14px;

                    @media (max-width: $breakpoint1) {
                        display: block;
                        text-align: center;
                        margin: 0;
                        width: 100%;
                    }

                    &.active {
                        color: #555;
                        border-bottom: 3px solid #1C91DC;

                        @media (max-width: $breakpoint1) {
                            text-decoration: underline;
                            border: none;
                        }
                    }

                    &:hover {
                        border-bottom: 3px solid rgb(211, 235, 250);

                        @media (max-width: $breakpoint1) {
                            border: none;
                            background:  rgb(211, 235, 250);
                        }
                    }

                    &:focus {
                        outline: 3px solid $focus-outline;
                    }
                }
            }
        }
    }

    &__right {
        display: flex;

        .object-search {
            padding-right: 10px;
            margin-top: 15px;

            @media (max-width: $breakpoint1) {
                padding: 5px;
                margin: 0;
            }

            input {
                width: 220px;
                float: right;

                @media (max-width: $breakpoint1) {
                    width: calc(100vw - 15px)
                }
            }
        }
    }
}

.user-tooltip {
    font-size: 28px;
    font-weight: 900;
    margin-top: -8px;
    max-height: 44px;

    @media (max-width: $breakpoint1) {
    }

    @media (max-width: $user-tooltip-hide-breakpoint) {
        display: none;
    }

    a {
        display: block;
        padding: 1px 0px;
        color: #444649;
    }

    button {
        all: unset;
        cursor: pointer;

        &:focus {
            outline-offset: 2px;
        }
    }
}