/* Positioning the user tooltip button */
.user-tooltip {
    position: relative;
    top: 57px;
}

div[mode="filter-selection"] .user-tooltip {
    top: 59px;
}

.content[mode="project-selection"] .header .user-tooltip {
    display: none;
}

.content[mode="project-selection"] .user-tooltip {
    position: relative;
    top: 4px;
    margin-left: 5px;
}

.project-chooser-header div {
    display: flex;
    align-items: center;
}

.top-bar__right {
    margin-right: 20px;
}

#root .content[mode="filter-selection"] #search-toolbar.search-toolbar {
    left: calc(100vw - 275px);
}

.content[mode="filter-selection"] .filter-panel-container .switch-bar {
    right: 220px;
}

.left-panel .folder-tree-container {
    padding-left: 20px;
}
