
@import url('https://fonts.googleapis.com/css?family=Roboto:300,350,400,700&display=swap');

html {
    --antd-wave-shadow-color: transparent;
}

body {
    margin: 0;
    background-color: #fff;
    font-family: 'Roboto', Arial, Verdana, Helvetica, sans-serif;
    font-size: 13px;
    color: #444649;
}

/* Container */
#root > .content {
    width: 100%;
    height: 100vh;
    margin: 0;
    display: grid;
    grid-template-rows: 50px auto 50px;
}

/* Header and Footer */
#root .header,
#root .footer {
    height: 50px;
    padding: 8px 10px 0 10px;
    width: 100vw;
}

#root .header {
    grid-row-start: 1;
    grid-row-end: 2;
    border-bottom: 1px solid #e5e5e5;
    z-index: 14;
    background: #fff;
    white-space: nowrap;
    display: flex;
}

.main-content:not(.authentication) {
    grid-row-start: 2;
    grid-row-end: 3;
    overflow: auto;
}

#root .footer {
    grid-row-start: 3;
    grid-row-end: 4;
    border-top: 1px solid #e7e7e7;
    z-index: 13;
    background: #fff;
}

#root .footer-content {
    display: flex;
    justify-content: space-between;
}

@media (max-width: 450px) {
    #root .footer {
        white-space: nowrap;
        overflow: auto;
    }
}

.header {
    display: flex;
    justify-content: space-between;
}

.header .title {
    line-height: 28px;
    font-size: 18px;
    vertical-align: top;
    padding: 0;
    color: #444649;
    overflow: hidden;
    text-overflow: ellipsis;
}

.header .mstr-logo {
    height: 28px;
    float: left;
    margin-right: 5px;
    float: left;
}

.header .mstr-logo svg {
    max-width: unset;
    max-height: unset;
}

.header .connector-logo {
    height: 28px;
    float: right;
}

.footer span {
    display: inline-block;
    cursor: initial !important;
}

.footer button {
    display: inline-block;
    cursor: pointer !important;
}

/* Main content */
.main-content {
    padding: 0 10px 0 10px !important;
}

.section-box {
    border: 1px solid rgba(151, 151, 151, 0.55);
    padding: 20px;
    /* Only for mstr dataset */
    padding-top: 10px;
    padding-bottom: 14px;
    font-size: 13px;
}

.ant-collapse-item {
    border: 0;
}

.ant-collapse-borderless > .ant-collapse-item > .ant-collapse-content > .ant-collapse-content-box {
    padding: 0;
    padding-left: 40px;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header .arrow {
    color: #979797;
    font-size: 18px;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header .arrow:before {
    content: "\25b8";
}

.ant-collapse > .ant-collapse-item {
    border: 0;
}

/* Form */
.ant-input {
    font-size: 13px;
}

.ant-form-item-label {
    text-align: left;
}

.ant-form-item {
    margin-bottom: 8px;
}

/* Layout helper elements */
.v-divider {
    display: inline-block;
    border-left: 1px solid rgba(151, 151, 151, 0.55);
    margin: 0 10px;
    height: 28px;
}

.h-divider {
    border-bottom: 1px solid rgba(151, 151, 151, 0.55);
    margin-bottom: 4px;
    height: 1em;
    width: 100%;
}

.ml-auto {
    float: right;
}

.border-right {
    border-right: 1px solid rgba(151, 151, 151, 0.55);
}

/* Search toolbar */
#root .filter-panel-container .search-toolbar {
    position: absolute;
    padding: 0 0 0 10px;
}

#root #search-toolbar.search-toolbar {
    position: relative;
    padding: 0;
    top: -56px;
    left: calc(100vw - 260px);
}

#root .filter-panel-container .search-toolbar-title {
    display: none;
}

.selected-element {
    display: inline-block;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 180px;
    line-height: 24px;
    height: 24px;
}

.selected-element-icon {
    padding: 0 5px 0 0;
}

.search-container {
    float: right;
}

.search-input {
    width: 200px;
    margin: 0 15px;
}

.switch-label {
  padding: 0 6px;
  cursor: pointer;
}

@media (max-width: 340px) {
    .switch-label {
        display: none;
    }
}

/* Selector */
.filter-panel-container {
    padding-bottom: 0;
}

.filter-panel-container .selector-title {
    font-size: 10px;
    margin: 20px 0 5px 0;
    white-space: nowrap;
}

.filter-panel-container .filter-panel-selectors {
    padding: 0;
    margin-top: -20px;
}

.filter-panel-container .filter-panel-selectors div:nth-child(1) {
    padding-left: 0 !important;
}

.filter-panel-container .filter-panel-selectors div:nth-child(3) {
    padding-right: 0 !important;
}

.filter-panel-container .checkbox-list .checkbox {
    line-height: 22px;
    padding-left: 5px;
}

.filter-panel-selectors .ant-empty {
    position: absolute;
    top: calc(50% + 30px);
    left: 50%;
    margin: 0 -50% 0 0;
    transform: translate(-50%, -50%)
}

.filter-panel-selectors .filter-list .ant-empty {
    top: calc(50% + 10px);
}

.filter-panel-container .switch-bar {
    position: absolute;
     top: -54px;
     right: 200px;
}

.ant-list,
.empty-list {
    border: 1px solid rgba(151, 151, 151, 0.55);
    border-radius: 0;
    font-size: 13px;
    height: 175px;
    max-height: 175px;
    overflow: auto;
}

.filter-panel-selectors .checkbox-list .selector-list {
    height: calc(100vh - 249px) !important;
    margin-top: 5px;
}

.filter-panel-container .checkbox-list,
.filter-panel-container .filter-list {
    border: 1px solid rgba(151, 151, 151, 0.55);
    height: calc(100vh - 220px);
    max-height: 100vh;
    overflow: hidden;
}

.filter-panel-container .filter-list {
    border-right: none;
}

.filter-panel-container .filter-list .ant-tree {
    height: calc(100vh - 222px);
}

.checkbox-list .checkbox {
    padding-right: 0 !important;
    padding-left: 16px;
    line-height: 24px;
    white-space: nowrap;
}

.checkbox-list .checkbox-empty {
    display: block;
    color: rgba(0, 0, 0, 0.4);
    width: 274px;
    padding-top: 16px;
    text-align: center;
}

.checkbox-list .ReactVirtualized__Grid__innerScrollContainer {
    max-width: none !important;
    display: table-row;
    margin-top: 3px;
}

.content .checkbox-list .mstr-icon.selector svg {
    margin-left: 1px;
    margin-right: 3px;
}

.filter-title-container .mstr-icon {
    line-height: 18px;
}

.ant-list-right {
    border-left: none;
}

.ant-list-bordered .ant-list-item {
    border: none;
    padding-top: 2px;
    padding-bottom: 2px;
}

.loading-indicator {
    height: 100%;
}

.loading-indicator-tree {
    height: 430px;
}

.ant-spin-nested-loading > div > .ant-spin {
    max-height: 430px;
}

.ant-spin-nested-loading,
.ant-spin-container {
    min-height: 173px;
    height: 100% !important;
}

.ant-spin-text {
  color: rgba(0, 0, 0, 0.5);
    font-size: 13px;
}

.ant-checkbox-wrapper,
span.ant-checkbox {
    font-size: 13px;
}

.ant-checkbox-wrapper {
    white-space: nowrap;
}

.ant-checkbox-inner {
    width: 13px;
    height: 13px;
}

.ant-checkbox-inner:after {
    left: 2.9px;
    top: 0;
    width: 5px;
    height: 8px;
}

.ant-checkbox-group {
    display: block;
}

/* Tree */
.ant-tree {
    font-size: 12px;
}

.ant-tree li {
    padding-top: 0;
    padding-bottom: 0;
}

.ant-tree-iconEle {
    font-weight: bold;
}

.folder-tree-container {
    max-height: 620px;
    height: 620px;
    overflow: auto;
}

.folder-tree-container:focus {
    background: rgb(24, 144, 255, 0.1);
}

.content .filter-list .ant-tree {
    font-size: 13px;
}

.content .filter-list .ant-tree li {
    display: inline-block;
    height: 25px;
}

.filter-list .ant-tree li .ant-tree-node-content-wrapper {
    padding: 0;
    line-height: 20px;
    transition: none;
}

.filter-list .ant-tree li,
.filter-list .ant-tree-node-content-wrapper.ant-tree-node-content-wrapper-normal {
    width: 100%;
}

.filter-list .ant-tree li .ant-tree-node-content-wrapper:focus {
    background-color: #e6f7ff;
}

.filter-list .ant-tree-switcher.ant-tree-switcher-noop {
    display: none;
}

.ant-tree > li:last-child,
.ant-tree > li:first-child {
    padding-top: 0;
    padding-bottom: 0;
}

.filter-list .filter-checkbox {
    display: inline-block;
    height: 24px;
    line-height: 24px;
    padding-left: 11px;
    vertical-align: middle;
}

.filter-list .filter-checkbox:before {
    margin-left: -7px;
}

.filter-list .ant-list {
    overflow: auto;
    overflow-x: hidden;
}

.ant-tree.ant-tree-directory .ant-tree-child-tree>li span.ant-tree-node-content-wrapper.ant-tree-node-selected, .ant-tree.ant-tree-directory>li span.ant-tree-node-content-wrapper.ant-tree-node-selected {
    background: #1890ff !important;
}

.ant-tree.ant-tree-directory .ant-tree-child-tree>li span.ant-tree-node-content-wrapper:hover,
.ant-tree.ant-tree-directory .ant-tree-child-tree>li span.ant-tree-node-content-wrapper:focus,
.ant-tree.ant-tree-directory>li span.ant-tree-node-content-wrapper:hover {
    background: rgb(24, 144, 255, 0.1);
}

.ant-tree.ant-tree-directory > li span.ant-tree-node-content-wrapper:focus {
    outline: none;
}

.ant-tree.ant-tree-directory li span.ant-tree-node-content-wrapper:not(.ant-tree-node-selected):focus:before {
    background: #e6f7ff;
}

.ant-tree.ant-tree-directory > li span.ant-tree-node-content-wrapper.ant-tree-node-selected:focus {
    color: rgba(0, 0, 0, 0.65);
}

/* Selector buttons */
.selector-button {
    border: none;
    margin: 0;
    padding-right: 0;
    padding-left: 10px;
    height: auto;
    font-size: 10px;
    line-height: 1;
    color: #1890ff;
}

.preview-button {
    border: none;
    margin-top: 10px;
    height: auto;
    color: #1890ff;
}

.preview-button .anticon,
.preview-button .anticon:hover {
    color: #979797;
}

/* Preview modal */
.ant-modal-mask {
    background-color: rgba(255, 255, 255, 0.7);
}

.ant-modal-content {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(151, 151, 151, 0.55);
}

.ant-modal-header {
    background-color: #ededed;
    padding: 8px 12px;
}

.ant-modal-title {
    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
}

.ant-modal-close-x {
    width: 36px;
    height: 36px;
    line-height: 36px;
}

/* Table */
.ant-table {
    font-size: 11px;
    font-weight: bolder;
    font-family: Arial, Helvetica, sans-serif;
}

.ant-table td {
    white-space: nowrap;
}

.ant-table-body {
    overflow-x: auto !important;
}

.modal-preview .ant-table-body {
    overflow-x: visible !important;
}

.modal-preview .ant-table-body .attribute-column {
    text-align: left;
}

.modal-preview .ant-table-content > .ant-table-scroll > .ant-table-body {
    margin: 0;
}

.modal-preview .ant-table-body .metric-column {
    text-align: right;
}

#mstr-table.ant-table-small > .ant-table-content > .ant-table-body > table {
    padding: 0;
}

#mstr-table .ant-table-thead > tr > th {
    background-color: #ededed;
    overflow: hidden;
    white-space: nowrap;
}

.preview-note {
    padding: 5px 0;
    font-size: 11px;
    text-align: right;
}

/* Icons */
.mstr-icon svg {
    max-width: 18px;
    max-height: 18px;
    vertical-align: text-top;
}

.mstr-icon.selector svg {
    max-height: 14px;
    vertical-align: text-bottom;
    margin-right: 5px;
}

.mstr-icon.dataset svg {
    max-width: 14px;
    vertical-align: text-bottom;
}

.mstr-icon.certified svg {
    margin-left: 0;
    vertical-align: text-bottom;
}

.authentication .mstr-logo svg {
    width: 70px;
    height: 70px;
    max-width: unset;
    max-height: unset;
}

/* Tooltip */
.info-tooltip {
    z-index: 13;
}

.info-tooltip.one-liner {
    max-width: unset;
}

.ant-tooltip-inner {
    font-size: 12px;
    min-height: unset;
    padding: 3px 5px;
    background: #000;
}

.ant-tooltip-placement-top .ant-tooltip-arrow,
.ant-tooltip-placement-topLeft .ant-tooltip-arrow,
.ant-tooltip-placement-topRight .ant-tooltip-arrow {
    border-top-color: #000;
}

.ant-tooltip-placement-bottom .ant-tooltip-arrow,
.ant-tooltip-placement-bottomLeft .ant-tooltip-arrow,
.ant-tooltip-placement-bottomRight .ant-tooltip-arrow {
    border-bottom-color: #000;
}

.ant-tooltip-placement-right .ant-tooltip-arrow,
.ant-tooltip-placement-rightTop .ant-tooltip-arrow,
.ant-tooltip-placement-rightBottom .ant-tooltip-arrow {
    border-right-color: #000;
}

.ant-tooltip-placement-left .ant-tooltip-arrow,
.ant-tooltip-placement-leftTop .ant-tooltip-arrow,
.ant-tooltip-placement-leftBottom .ant-tooltip-arrow {
    border-left-color: #000;
}

.ant-tooltip.no-arrow .ant-tooltip-arrow {
    display: none;
}

/* Version tag */
#version {
    display: inline-block;
    font-size: 12px;
    padding-left: 10px;
    vertical-align: text-bottom;
    height: 25px;
    color: rgba(0, 0, 0, 0.25);
}

/* Tooltip */
.rc-tooltip-inner {
    padding: 20px 20px;
    color: #444649;
    text-align: left;
    text-decoration: none;
    background-color: #ffffff;
    border-radius: 6px;
    -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.17);
    min-height: 34px;
  border: solid #bebfc0 1px;
    border-radius: 4px;
    box-shadow: 0 0 8px 0 #827d7d;
}

.rc-tooltip-placement-topLeft .rc-tooltip-arrow {
    left: 35px;
    bottom: 10px;
    margin-left: -5px;
    border-width: 0 0 0;
    border-top-color: transparent;
}

.rc-tooltip-arrow:after,
.rc-tooltip-arrow:before {
	top: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.rc-tooltip-arrow:after {
	border-color: rgba(255, 255, 255, 0);
	border-top-color: #ffffff;
	border-width: 6px;
	margin-left: -6px;
}
.rc-tooltip-arrow:before {
	border-color: rgba(82, 82, 82, 0);
  border-top-color: #bebfc0;
	border-width: 9px;
    margin-left: -9px;
}

.tooltip-header {
    display: inline-block;
    text-align: right;
  color: #a2a2a4;
    padding-right: 15px;
}

.tooltip-description {
    display: inline-block;
}

.rc-tooltip {
    opacity: 1;
}

.tooltip-first-line {
    margin-bottom: 7px;
}

/* filters */
.filter-title-container {
    display: flex;
    min-width: 257px;
}

.filter-title {
    overflow: hidden;
    text-overflow: ellipsis;
    flex-grow: 1;
    line-height: 21px;
}

.ant-tree-title {
    display: inline-block;
    width: 100%;
}

.filter-list {
    overflow-x: hidden;
}
.rstudio-connector-params label {
    font-size: 13px;
  color: rgba(0, 0, 0, 0.65);
}

/* FolderBrowser */
.content .table-container.object-browser {
    margin: 0;
    overflow: hidden;
}

.content .ant-table-fixed-header .ant-table-scroll .ant-table-header {
    overflow: auto;
    z-index: 12;
    margin-bottom: 0;
    padding-bottom: 0;
    position: relative;
    padding: 0 15px;
    background: #fff;
    border-bottom: 1px solid #e8e8e8
}

.ant-table-header .ant-table-fixed {
    min-width: calc(100% - 5px);
}

.content[mode="browsing"] .ant-table-thead > tr > th {
    border-bottom: none;
}

#connectButton.ml-auto {
  float: left;
}

/* remove doubled search icon */
#search-toolbar > div > span > span > i.anticon-search:before {
    content: none;
}

.content .ant-row.full-height,
.context .ant-row .full-height {
  height: calc(100vh - 159px);
  width: 100vw;
}

.footer button {
    margin-left: 8px;
}

.footer > button {
    margin: 0;
}

.content .smart-folders-empty-table .ant-table-placeholder {
    border: 0;
}

.ant-modal-content .ant-table-wrapper {
    max-height: calc(100vh - 300px);
    overflow: auto;
}

.ant-modal {
    position: relative;
    top: 60px;
}
.ant-modal-close-icon:before {
    content: none;
}

.ant-btn {
    min-width: 90px;
}

.flex-container {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

div .ant-tooltip.info-tooltip .ant-tooltip-inner {
    text-align: center;
}

div .ant-tooltip.hidden {
    display: none;
}

.center-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.content .folder-browser-title {
    max-width: calc(100vw - 313px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: none;
    height: auto;
    padding: 11px 20px 11px 12px;
}

@media (max-width: 400px) {
    .content .folder-browser-title {
        font-size: 0;
        text-indent: -1000em;
    }
}

.content .filter-panel-container {
    border-top: 1px solid #E8EDF0;
}



/* Data preview */
.modal-preview .ant-table-content > .ant-table-scroll > .ant-table-body {
    overflow-y: initial;
}

.modal-preview .ant-table-scroll {
    overflow: unset;
    overflow-x: visible;
}

.modal-preview .ant-table-small.ant-table-bordered {
    border: none;
}

.modal-preview .ant-table-small.ant-table-bordered .ant-table-content {
    border: none;
}

.modal-preview .ant-table-fixed {
    border: 1px solid #e8e8e8 !important;
}

.modal-preview #mstr-table .ant-table-thead>tr>th {
    font-size: 15px;
}

.modal-preview #mstr-table .ant-table-tbody>tr>td {
    font-size: 14px;
}

.modal-preview .ant-radio-button-wrapper:hover {
    color: initial;
    background: #F3F3F3;
}

.modal-preview tbody .metric-column,
.modal-preview tbody .attribute-column {
    max-width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}
/* Remove animations/glows from buttons */
.content .ant-btn:focus {
    outline: 3px solid #1890ff;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

.content .ant-btn:after {
    display: none;
}

.ant-checkbox {
    animation: none;
}

.ant-tree-checkbox-checked:after,
.ant-checkbox-checked:after,
.ant-radio-checked:after {
    animation: none;
    border: none;
    transition: none;
}

.ant-checkbox-inner,
.ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-tree-checkbox-inner:after,
.ant-tree-checkbox-checked .ant-tree-checkbox-inner:after,
.ant-checkbox-checked .ant-checkbox-inner:after,
.ant-tree-checkbox-inner,
.ant-radio-checked .ant-radio-inner:after,
.ant-radio-inner,
.ant-radio-inner:after {
    transition: none;
}

.ant-radio-input:focus + .ant-radio-inner {
    box-shadow: none;
}

input[tabfocused="1"] + .ant-radio-inner,
input[tabfocused="1"] + .ant-checkbox-inner {
    outline: 3px solid #1890ff;
    outline-offset: 1px;
    box-shadow: none;
}

/* Search input */
.content .ant-input-affix-wrapper .ant-input:not(:last-child) {
    padding-left: 30px;
    padding-right: 7px;
}

.content .ant-input-affix-wrapper .ant-input-suffix {
    left: 12px;
    right: 0;
    width: 16px;
}

/* Loading modal */
.loading-modal-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading-modal-spin {
    padding-right: 20px;
}

.loading-modal-text p {
    margin: 0;
}

/* Checkboxes in the data preparation pane */
.checkbox-list label.checkbox {
    cursor: pointer;
}

.checkbox-list input[type='checkbox'] {
    position: absolute;
    cursor: pointer;
    height: 0;
    width: 0;
    top: 4px;
}

.checkbox-list span.mstr-icon {
    padding-left: 17px;
}

.checkbox-list .ant-empty-image span.mstr-icon {
    padding: 0;
}

.checkbox-list .checkbox input[type='checkbox']:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 14px;
    width: 14px;
    border: 1px solid #d9d9d9;
    background: #fff;
    border-radius: 2px;
    cursor: pointer;
}

.checkbox-list input[type='checkbox']:focus,
.checkbox-list input[type='checkbox'][tabfocused="1"] {
    outline: none !important;
}

.checkbox-list input[type='checkbox'][tabfocused="1"]:before {
    outline: 3px solid #1890ff;
    outline-offset: 2px;
    box-shadow: none;
    border-color: #1890ff;
}

.checkbox-list .checkbox input[type='checkbox']:checked:before {
    background-color: #1890ff;
    border-color: #1890ff;
}

.checkbox-list .checkbox:hover input[type='checkbox']:before {
    border-color: #1890ff;
}

.checkbox-list input[type='checkbox']:after {
    left: 4px;
    top: 2px;
    width: 6px;
    height: 9px;
    border: 1px solid white;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
    content: "";
    position: absolute;
    display: none;
}

.checkbox-list input[type='checkbox']:checked:after {
    display: block;
}

label.checkbox span:last-of-type {
    overflow: hidden;
    text-overflow: ellipsis;
}

.checkbox span::selection {
    background: transparent;
    color: inherit;
}

button:focus {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

button:focus:not(.focus-visible) {
    outline: none !important;
    box-shadow: none !important;
    transition: none !important;
}

[tabfocused="1"] {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
    box-shadow: none;
    transition: none;
}

.ant-btn:focus:not(.some) {
    box-shadow: none;
}

.whole-page-loading {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

input[type="checkbox"].focus-visible {
    box-shadow: none !important;
}
