@import url("https://fonts.googleapis.com/css?family=Roboto");

/* Authentication */
.main-content.authentication {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-top: 50px !important;
}

.mstr-logo-authentication {
  width: 70px;
  height: 70px;
}

.authentication h1 {
  font-size: 20px;
  margin-top: 16px;
  margin-bottom: 35px;
}

.authentication h2 {
  font-size: 14px;
  margin-bottom: 33px;
}

.authentication-form {
  width: 303px;
  margin: 0 auto;
  padding-bottom: 5px;
}

.authentication-form--hidden {
  display: none;
}

.authentication-form .ant-form-item-control-wrapper {
  width: 100%;
}

.authentication-form input {
  border: none;
  border-bottom: 1px solid #e2e2e2;
  border-radius: 0;
  padding-left: 0;
  padding-right: 0;
}

.authentication-form input:focus,
.authentication-form input:hover {
  border: none;
  border-bottom: 1px solid #444649;
  border-radius: 0;
}

.authentication .authentication-form .ant-select-search__field {
  border-radius: 0;
}

.ant-select-selection {
  box-shadow: none;
}

#root .content .authentication .authentication-form div,
#root .content .authentication .authentication-form input {
  box-shadow: none;
  font-family: "Roboto";
  font-size: 14px;
  text-overflow: ellipsis;
}

.ant-form-item-label label {
  font-size: 12px;
  color: #333333;
  opacity: 0.5;
}

.authentication
  .ant-select-auto-complete.ant-select
  .ant-select-selection__placeholder {
  margin-left: 0;
  margin-right: 0;
}

.authentication-form-credentials {
  margin-top: 20px;
}

.authentication-form-credentials > .ant-form-item:first-child {
  margin-bottom: 0;
}

.authentication-form-credentials input[placeholder="Username"] {
  margin-top: 5px;
}

.authentication-form-credentials input.invalid,
#root .content #authForm .has-error .ant-form-explain,
.incorrect-credentials-info p {
  color: rgba(217, 35, 46, 1);
  font-size: 12px;
}

#root .content #authForm .has-success .ant-form-explain {
  display: none;
}

#root .content #authForm .has-error .ant-form-explain span:not(.mstr-icon) {
  font-size: 12px;
  line-height: 17px;
}

.authentication-form .ant-btn.ant-btn-primary {
  border-radius: 8px;
}

.red-info svg {
  width: 17px;
  height: 17px;
  margin-right: 5px;
}

.incorrect-credentials-info {
  height: 35px;
  transition: height 0.2s linear;
}

.incorrect-credentials-info.invisible {
  height: 0;
  transition: height 0.2s linear;
}

/* Popover */
.recent-environments {
  width: 350px;
}

.recent-environments ul {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  max-height: 132px;
  overflow: auto;
  border-top: 1px solid rgba(51, 51, 51, 0.05);
  border-bottom: 1px solid rgba(51, 51, 51, 0.05);
}

.recent-environments ul li {
  padding: 7px 5px 7px 20px;
  display: flex;
  text-align: left;
}

.recent-environments li:hover,
.recent-environments li.selected {
  background-color: rgba(51, 51, 51, 0.05);
}

.recent-environments ul li div:first-child {
  overflow: hidden;
  font-size: 12px;
  color: rgba(51, 51, 51, 0.5);
  width: 98%;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0;
  cursor: pointer;
}

.recent-environments .recent-env-button h3,
.recent-environments .recent-env-button p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 305px;
}

.recent-environments .recent-env-button p {
  color: #919191;
  color: rgba(51, 51, 51, 0.5);

}

.recent-environments ul li.selected p,
.recent-environments .recent-env-button:hover p {
  color: #000;
}

.recent-environments .remove-env-button {
  font-size: 14px;
  line-height: 51px;
  color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  padding-left: 5px;
}

.recent-environments .remove-env-button i:hover {
  color: #000;
}

.recent-environments h3 {
  font-size: 14px;
  margin: 0;
}

body .ant-popover.recent-environments .ant-popover-inner-content {
  background-color: #fff;
  padding: 0;
}

body .ant-popover-arrow {
  display: none;
}

.recent-environments--hidden {
  display: none;
}

.recent-environments .clear-recent {
  font-size: 12px;
  padding: 5px 5px 5px 20px;
  text-align: left;
}

.recent-environments button {
  border: none;
  background: none;
  padding: 0;
}

.recent-environments .clear-recent button {
  color: #1890ff;
  cursor: pointer;
}

.recent-environments .recent-environment-list button {
  text-align: left;
  cursor: pointer;
}

.recent-environments .ant-popover-title {
  color: #919191;
  border: none;
  padding: 10px 5px 10px 20px;
}

.ant-popover .ant-popover-content {
  font-family: "Roboto", Arial, Verdana, Helvetica, sans-serif;
}

/* Confirmation modal */
.confirm-wrapper {
  display: flex;
  font-family: "Roboto", Arial, Verdana, Helvetica, sans-serif;
}

.confirm-info {
  padding-left: 20px;
  line-height: 35px;
}

.confirm-info p {
  margin-bottom: 5px;
}

.confirm-icon i {
  font-size: 35px;
  color: rgba(217, 35, 46, 1);
}

.confirm-title {
  font-size: 16px;
  font-weight: 900;
}

.confirm-modal .ant-modal-body {
  padding-bottom: 0;
}

/* Radio buttons */
.authentication .ant-radio {
  vertical-align: middle;
  display: none;
}

.authentication .ant-radio-group:focus {
  outline: 3px solid #1890ff;
  outline-offset: 3px;
  box-shadow: none;

}

.authentication .ant-radio-inner {
  border-color: #cecece;
  width: 13px;
  height: 13px;
}

.authentication #loginMode .ant-radio-wrapper {
  line-height: 16px;
}

.authentication #loginMode .ant-radio-wrapper:first-child {
  margin-right: 70px;
}

.authentication .ant-radio-wrapper-checked {
  color: #2094DD;
  border-bottom: 2px solid #2094DD;

}

.authentication .ant-radio-wrapper span {
  padding: 0 0;
}

.authentication .ant-radio-inner:after {
  background-color: #444649;
  width: 7px;
  height: 7px;
  top: 2px;
  left: 2px;
}

.authentication .ant-radio-wrapper:hover .ant-radio .ant-radio-inner, 
.authentication .ant-radio:hover .ant-radio-inner, 
.authentication .ant-radio-focused .ant-radio-inner
.authentication .ant-radio-checked:after,
.authentication .ant-radio-checked .ant-radio-inner {
  border: 1px solid #444649;
}

/* Connect button */
#connectButton {
  width: 303px;
  height: 42px;
}

#connectButton:hover {
  border-radius: 8px;
}

#connectButton.invalid {
  top: 0;
}