.table-container {
    margin: 0 22px 22px;
    overflow: auto;
    height: calc(100% - 80px);
    white-space: nowrap;
}

.table-container .ant-table {
    color: #555;
    font-size: 14px;
    font-weight: normal;
}

.table-container .ant-table-thead>tr>th {
    border-top: none;
    background: #fff;
    font-weight: 400;
    font-size: 12px;
    color: #627282;
}

.table-container .ant-table-tbody>tr.ant-table-row-selected td, .table-container .ant-table-tbody>tr.ant-table-row-selected:hover td,
.table-container .ant-table-thead > tr > th.ant-table-column-sort,
.table-container .ant-table-tbody > tr > td.ant-table-column-sort,
.table-container .ant-table-thead > tr > th.ant-table-column-sort,
.table-container .ant-table-thead > tr > th.ant-table-column-has-actions.ant-table-column-has-sorters:hover {
    background: #fff;
}

.content .table-container .ant-table-body {
    height: calc(100vh - 161px);
    max-height: calc(100vh - 161px);
}

.table-container .ant-table-tbody {
    color: #444649;
    line-height: 30px;
}

.table-container .ant-table-tbody>tr {
    cursor: pointer;
}

.table-container .ant-pagination.ant-table-pagination {
    display: none;
    position: fixed;
    bottom: 58px;
    right: 20px;
}

.table-container .loading-text-container {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    background-color: transparent;
}

.table-container .ant-spin-nested-loading>div>.ant-spin {
    max-height: unset;
}

@media (max-width: 576px) {
    .table-container .ant-pagination.ant-table-pagination {
        position: static;
        bottom: auto;
        right: auto;
    }

    .table-container {
        height: auto;
    }

    .table-container .ant-table-body {
        height: calc(100vh - 348px);
        max-height: calc(100vh - 348px);
    }
}

.table-container .ant-table td,
.table-container .ant-table td>div {
    white-space: nowrap !important;
    line-height: normal;
    vertical-align: top;
    text-overflow: ellipsis;
    overflow: hidden;
}

.smart-folders-empty-table .ant-table-header {
    visibility: hidden;
}

.smart-folders-empty-table {
    height: calc(100% - 70px);
    display: flex;
    align-items: center;
    justify-content: center;
}

@media(max-width: 500px) {
    .smart-folders-empty-table {
        height: calc(100vh - 500px);
    }
}

.smart-folders-empty-table .ant-spin-nested-loading {
    max-width: 100%;
}

/* Browsing table */
.dataframe-selection .ant-checkbox-inner,
.left-panel .ant-checkbox-inner {
    width: 14px;
    height: 14px;
}

.dataframe-selection .ant-checkbox-indeterminate .ant-checkbox-inner:after,
.left-panel .ant-checkbox-indeterminate .ant-checkbox-inner:after {
    width: 8px;
    height: 8px;
    left: 6px;
    top: 6px;
}

.left-panel::-webkit-scrollbar {
    width: 0;
    background: transparent;
}

.ant-table-content > .ant-table-scroll > .ant-table-header::-webkit-scrollbar {
    height: 0;
    background: transparent;
}

.ant-table-content > .ant-table-scroll > .ant-table-body {
    max-height: calc(100vh - 209px);
    overflow: auto;
}

@media (max-width: 500px) {
    .ant-table-content > .ant-table-scroll > .ant-table-body {
        max-height: calc(100vh - 240px);
        overflow: auto;
    }
}

.browser-content .ant-table-tbody > tr > td,
.browser-content .ant-table-header th {
    padding: 16px 2px;
}

.browser-content .ant-table colgroup > col.ant-table-selection-col {
    min-width: 30px;
    width: 30px;
}

.ant-table-selection-column {
    padding-top: 13px !important;
    padding-left: 7px !important;
    min-width: 30px;
    width: 30px;
}

/* Icon header, icon cell */
.icon-column {
    text-align: center;
    min-width: 30px;
    padding-right: 7px !important;
    padding-top: 15px !important;
}

/* Name column */
.name-column {
    min-width: 136px;
    overflow: hidden;
    padding-right: 4px;
}

/* Certification column */
.certification-column {
    min-width: 55px;
    width: 25px;
}

.certification-column .mstr-icon.certified svg {
    margin-left: 4px;
}

/* Owner column */
.owner-column {
    min-width: 160px;
}

/* Application column */
.application-column {
    min-width: 180px;
}

/* Modified column */
.browser-content .modified-column {
    min-width: 150px;
    width: 150px;
    max-width: 150px;
}

.ant-table-header .modified-column {
    min-width: 180px;
}

@media (max-width: 940px) {
    /* Certification column */
    .certification-column {
        display: none !important;
    }

    .browser-content .modified-column {
        min-width: 100px;
        width: 100px;
        max-width: 100px;
    }
}

@media (max-width: 860px) {
    .application-column {
        display: none !important;
    }
}


@media (max-width: 680px) {
    .owner-column {
        display: none !important;
    }
}

@media (max-width: 530px) {
    .icon-column {
        display: none;
    }

    .browser-content .modified-column {
        max-width: 100px;
        width: 100px;
        min-width: 100px;
    }

    .ant-table-header .modified-column {
        min-width: 100px;
    }

    .browser-content .modified-column div {
        max-width: 100px !important;
        white-space: initial !important;
    }
}

.ant-table-content > .ant-table-scroll > .ant-table-body {
    margin: 0 15px;
}

::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 5px;
    height: 5px;
}

::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background-color: rgba(0,0,0,.5);
    -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
}

.dataset-props-popover {
    color: #000;
    overflow: visible;
    padding-bottom: 5px;
    font-size: 14px;
    font-weight: 400;
}

.dataset-props-popover .ant-modal-header {
    background: #fff;
    padding: 18px 0 16px 24px;
    font-size: 14px;
}

.dataset-props-popover .ant-modal-title {
    color: rgba(68,70,73,1);
    font-size: 14px;
}

.dataset-props-popover .ant-modal-body {
    padding: 0;
}

.dataset-props-popover .ant-modal-close {
    top: 9px;
}

.dataset-props-popover table {
    margin: 10px 10px 10px 24px;
    border-collapse: separate;
    border-spacing: 0 5px;
}

.dataset-props-popover table tr td:nth-child(1) {
    color: rgba(68,70,73,0.5);
    width: 100px;
}

.dataset-props-popover table tr:nth-child(1) td:nth-child(2) {
    font-weight: 700;
}

.properties-context-menu {
    position: absolute;
    border-radius: 4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15)
}

.properties-context-menu button {
    display: block;
    padding: 10px;
    font-size: 14px;
    color: #000;
    background: #fff;
    border: none;
}

.properties-context-menu button:hover {
    color: rgba(68,70,73,0.5);
}

.ant-modal-body .ant-table-content > .ant-table-scroll > .ant-table-body {
    max-height: none;
}

.import .ant-table-placeholder {
    padding: 0;
    background: transparent;
    height: calc(100vh - 158px);
    margin-top: -49px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 500px) {
    .import .ant-table-placeholder {
        height: calc(100vh - 189px);
    }
}

.no-data-placeholder .mstr-icon svg {
    width: 117px;
    height: 82px;
    max-width: none;
    max-height: none;
}

.no-data-placeholder p {
    font-size: 14px;
    font-weight: 400;
}

.no-data-placeholder p:nth-child(2) {
    font-weight: 700;
    margin: 10px 0;
}

/* Dataset name tooltip */
div .ant-tooltip.dataset-name-tooltip {
    z-index: 11;
}

.object-browser .ant-radio-inner {
    width: 14px;
    height: 14px;
}

.object-browser .ant-radio-checked .ant-radio-inner {
    background: #1890ff;
}

.object-browser .ant-radio-checked .ant-radio-inner:after {
    background-color: #fff;
    width: 4px;
    height: 4px;
    top: 4px;
    left: 4px;
}