.ant-row.full-height {
    height: 100%;
}

.ant-table-fixed-header .ant-table-scroll .ant-table-header {
    overflow-y: hidden;
}

.import-header {
    display: flex;
}

.ant-table-placeholder {
    border-bottom: none;
}

.ant-table-column-sorters {
    display: flex !important;
    align-items: center;
    justify-content: space-between;
}

.ant-table-column-sorter .off {
    opacity: 0.2;
    display: block;
}

.ant-table-column-sorter .on {
    display: block;
}

.ant-table-column-title {
    display: ruby-base !important;
}

@media (max-width: 576px) {
    .ant-row.full-height {
        height: auto;
    }

    .ant-row>.full-height {
        height: auto;
    }
}

.browser-content {
    margin-top: -10px;
}

#root .content .left-panel {
    background: #EEF1F3;
    height: calc(100vh - 158px);
    overflow: auto;
    border-right : 1px solid #e7e7e7;
    width: 235px;
    overflow-x: hidden;
    overflow-y: auto;
}

@media (max-width: 500px) {
    #root .content .left-panel {
        height: calc(100vh - 189px);
    }
}

.content .left-panel .dataset-properties {
    height: calc(100vh - 158px);
    overflow: auto;
    padding-left: 10px;
    background: none;
    width: 234px;
}

@media (max-width: 500px) {
    .content .left-panel .dataset-properties {
        height: calc(100vh - 189px);
    }
}

.content .left-panel .dataset-properties .ant-collapse-content .ant-collapse-content-box {
    padding-left: 0;
}

.content .left-panel .dataset-properties input[type="text"] {
    margin: 6px;
}

.left-panel-view-wrapper .ant-collapse-item:last-child  .ant-collapse-header:not(.some) {
    margin-bottom: 0;
}

/* Collapse panel header */
.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header {
    padding: 0;
    margin: 5px;
    font-size: 12px;
    color: #627282;
}

.left-panel .ant-collapse > .ant-collapse-item:first-child > .ant-collapse-header {
    margin-top: 10px;
}
.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header:focus {
    outline: 3px solid #1890ff;
    outline-offset: 1px;
    box-shadow: none;
    transition: none;
}

.left-panel .ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
    left: 200px;
}

.left-panel table {
    font-size: 14px;
    color: #515356;
}

.left-panel label {
    cursor: pointer;
    -ms-user-select: none;
    user-select: none;
}

/* Checkbox */
.dataframe-selection .ant-checkbox-inner:after,
.left-panel table .ant-checkbox-inner:after {
    width: 1px;
    height: 9px;
    left: 2px;
    top: 6px;
}

.left-panel table td {
    padding: 0 0 5px 5px;
}

.left-panel h4:not(:nth-child(1)) {
    margin-top: 10px;
}

.left-panel table tr td:nth-child(2) {
    width: 145px;
    max-width: 145px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-left: 5px;
    vertical-align: middle;
    height: 14px;
}

.left-panel table tr td:nth-child(3) {
    width: 50px;
    text-align: right;
}

.left-panel .ant-input-search {
    width: 205px;
    margin-bottom: 7px;
}

.content .right-panel {
    width: calc(100vw - 235px);
}

#manage-applications {
    display: block;
    width: 140px;
    text-align: center;
    margin: 5px auto;
    border: 1px solid #627282;
    color: #627282;
    padding: 1px;
    border-radius: 2px;
    font-size: 12px;
}

/* Folder tree */
.left-panel .folder-tree-container {
    min-height: calc(100vh - 158px);
    max-height: calc(100vh - 158px);
    overflow: auto;
    margin: 0 0 0 -18px;
    padding: 10px 0 0 15px;
    width: 252px;
}

.left-panel .folder-tree {
    background: none;
    border: none;
    overflow: visible;
    min-height: calc(100vh - 180px);
    float: left;
    min-width: 100%;
}

.left-panel .loading-indicator-tree {
    height: 100%;
}

.left-panel .folder-tree li span.ant-tree-node-content-wrapper {
    padding: 0;
    margin-left: -5px;
    min-height: 24px;
    height: inherit;
}

.folder-tree .ant-tree-child-tree > li:first-child {
    padding-top: 0;
}

.left-panel-view-wrapper {
    height: calc(100vh - 200px);
    float: left;
}

.left-panel .left-panel-view-wrapper  .folder-tree-container {
    min-height: calc(100vh - 200px);
    max-height: calc(100vh - 200px);
}

.left-panel-view-wrapper .folder-tree {
    min-height: calc(100vh - 210px);
}

.left-panel .left-panel-switcher {
    width: 70px;
    margin: 0 auto;

}

.left-panel .left-panel-switcher a {
    width: 35px;
    height: 35px;
}

.left-panel .left-panel-switcher span svg {
    display: inline-block;
    max-width: unset;
    max-height: unset;
    width: 30px;
    height: 30px;
}

.left-panel .left-panel-switcher span.disabled svg {
    padding: 7px;
}