.left-panel {
    border-right: none !important;
}

.wrangler-content .right-panel {
    height: calc(100vh - 158px);
}

.top-nav .wrangle-title {
    line-height: 58px;
    font-size: 18px;
    padding: 0;
    color: #444649;
    overflow: hidden;
    text-overflow: ellipsis;
}

div[mode="export-data-wrangler"] .top-bar__right {
    align-items: center;
}

div[mode="export-data-wrangler"] .object-search {
    margin: 0 0 -5px 0;
}

div[mode="export-data-wrangler"] button[role="switch"] {
    margin-right: 10px;
    min-width: 35px;
}

button.focus-visible:focus {
    box-shadow: none;
}

.align-top {
    margin-top: -10px;
}

.ant-btn.dataframe-item,
.ant-btn.dataframe-item-selected {
    overflow: hidden;
    white-space: nowrap;
    text-align: left;
    text-overflow: ellipsis;
    padding: 0 9px 0 40px;
    margin: 0;
    height: 45px;
    width: 100%;
    line-height: 45px;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
}

.ant-btn.dataframe-item-selected {
    background: #fff !important;
}

.ant-btn.dataframe-item,
.ant-btn.dataframe-item-selected,
.ant-btn.dataframe-item:focus,
.ant-btn.dataframe-item-selected:focus,
.ant-btn.dataframe-item:hover,
.ant-btn.dataframe-item-selected:hover {
    box-shadow: none;
    transition: none;
    background: transparent;
    box-shadow: none;
    border: none !important;
    border-radius: 0;
}

.ant-btn.dataframe-item.focus-visible,
.ant-btn.dataframe-item-selected.focus-visible {
    outline-offset: -3px !important;
}

.menu-title {
    font-size: 12px;
    font-weight: 400;
    color: #627282;
    padding: 19px 0 9px 17px;
}

.wrangler-content {
    display: flex !important;
    flex-wrap: nowrap;
}

.wrangler-content .inner-wrangler-content {
    display: flex;
    align-items: center;
    height: 60%;
}

.displaybox-holder .selector-title {
    white-space: nowrap;
    margin-bottom: 10px !important;
    font-weight: normal !important;
}

.displaybox-holder {
    padding: 21px 24px 16px;
    flex-basis: 100%;
    align-self: flex-start;
    height: 100%;
    overflow: hidden;
}

.displaybox-holder .displaybox-content {
    border: solid 1px #dadada;
    padding: 6px 12px;
    height: calc(100% - 24px);
    overflow-y: auto;
}

.displaybox-holder .await-drop {
    outline: solid 2px #d9232e;
    opacity: 0.5;
}

.displaybox-holder .main-list {
    list-style: none;
    margin: 0;
    padding: 0;
    white-space: nowrap;
}

.displaybox-holder li {
    line-height: 14px;
    padding: 9px 3px;
}

.displaybox-holder svg, 
.table-holder svg {
    height: 14px;
    min-height: 14px;
    width: 14px;
    min-width: 14px;
    margin-right: 8px;
}

/* Checkboxes in the wrangle data pane */
.checkbox {
    cursor: pointer;
    display: flex;
}

.checkbox input[type='checkbox'] {
    height: 0;
    width: 0;
    margin-right: 23px;
}

.checkbox input[type='checkbox']:before {
    content: "";
    height: 14px;
    width: 14px;
    border: 1px solid #d9d9d9;
    background: #fff;
    border-radius: 2px;
    cursor: pointer;
    display: inline-block;
}

.checkbox input[type='checkbox']:focus,
.checkbox input[type='checkbox'][tabfocused="1"] {
    outline: none !important;
}

.checkbox input[type='checkbox'][tabfocused="1"]:before {
    outline: 3px solid #1890ff;
    outline-offset: 2px;
    box-shadow: none;
    border-color: #1890ff;
}

.checkbox input[type='checkbox']:checked:before {
    background-color: #1890ff;
    border-color: #1890ff;
}

.checkbox:hover input[type='checkbox']:before {
    border-color: #1890ff;
}

.checkbox input[type='checkbox']:after {
    width: 6px;
    height: 9px;
    border: 1px solid white;
    border-width: 0 2px 2px 0;
    transform: translate(4px, -16px) rotate(45deg);
    content: "";
    position: relative;
    display: none;
}

.checkbox input[type='checkbox']:checked:after {
    display: block;
}

.checkbox span::selection {
    background: transparent;
    color: inherit;
}

.displaybox-holder .dragged-over {
    outline: solid 2px #d9232e;
}

.displaybox-holder span {
    padding: 0;
    font-size: 12px;
}

.displaybox-holder .focus-visible,
.displaybox-holder span.focus-visible,
.displaybox-holder .new-col-name-box.focus-visible {
    outline: 3px solid #1890ff !important;
    outline-offset: 3px;
}

.table-holder th.focus-visible,
.table-holder .new-col-name-box.focus-visible {
    outline: 3px solid #1890ff !important;
    outline-offset: -3px;
}

.arrow-holder {
    transform: translateY(12px)
}

.arrow-holder button {
    margin: 9px 0;
    min-width: 18px !important;
    height: 18px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
    padding: 0;
    line-height: 0;
}

.arrow-holder button,
.arrow-holder button:focus,
.arrow-holder button.focus-visible,
.arrow-holder button:hover {
    box-shadow: none;
    transition: none;
    background: transparent;
    border: none;
}

.displaybox-holder span:focus:not(.focus-visible),
.displaybox-holder .new-col-name-box:focus:not(.focus-visible),
.table-holder th:focus:not(.focus-visible),
.table-holder .new-col-name-box:focus:not(.focus-visible),
.arrow-holder button:focus:not(.focus-visible) {
    outline: none !important;
}

.main-splitter {
    background: #e8edf0;
    height: 1px;
    margin-left: 4px;
    width: calc(100% - 4px);
}

.table-holder {
    height: calc(40% - 1px);
    margin: 0 24px;
    padding: 15px 0;
    overflow: auto;
    max-width: 100%;
    vertical-align: top;
}

.table-holder .th-dragged-over {
    outline: solid 2px #d9232e;
}

.table-holder .await-drop {
    border: solid 2px #d9232e;
    opacity: 0.7;
}

.table-holder svg {
    vertical-align: inherit;
}

.table-holder th {
    padding: 7px 10px;
    font-size: 12px;
    font-weight: 400;
    background: #f7f9fa;
    min-width: 75px;
    height: 27px;
}

.table-holder .new-col-name-box,
.displaybox-holder .new-col-name-box {
    background: transparent;
    border: none;
    font-size: 12px;
    font-weight: 700;
    margin: 0;
}

.table-holder .new-col-name-box {
    padding: 6px;
    width: 100%;
}

.displaybox-holder .new-col-name-box {
    padding: 0;
    min-width: 60px;
    width: 50%;
}

.table-holder td {
    font-size: 12px;
    height: 25px;
    vertical-align: middle;
    padding: 0 10px;
}

.table-holder td,
.table-holder th {
    border: solid 1px #ebeff2;
    white-space: nowrap;
}

.table-holder .align-right {
    text-align: right;
}

.loader-holder {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.hidden-obj {
    display: none !important;
}
