/* Container */
.box-container {
    width: 1032px;
    margin-top: 40px;
    background-color: #ffffff;
    margin: 0 auto;
}

/* Header and Footer */
.header,
.footer {
    padding: 28px 24px;
    width: 100%;
}

.header {
    border-bottom: 1px solid #979797;
    height: 85px;
}

.footer {
    height: 89px;
}

.header .title {
    font-size: 18px;
    vertical-align: top;
    padding: 0;
    color: #444649;
    display: inline-block;
}

.header .mstr-logo {
    height: 28px;
    float: left;
}

.header .connector-logo {
    height: 28px;
    float: right;
}

.footer {
    border-top: 1px solid #979797;
}

.footer button {
    display: inline-block;
}

/* Main content */
.main-content {
    padding: 18px 64px 30px 44px;
}

.section-box {
    border: 1px solid rgba(151, 151, 151, 0.55);
    padding: 20px;
    /* Only for mstr dataset */
    padding-top: 10px;
    padding-bottom: 14px;
    font-size: 13px;
}

.ant-collapse-item {
    border: 0;
}

.ant-collapse-borderless>.ant-collapse-item>.ant-collapse-content>.ant-collapse-content-box {
    padding: 0;
    padding-left: 40px;
}

.ant-collapse>.ant-collapse-item>.ant-collapse-header .arrow {
    color: #979797;
    font-size: 18px;
    left: 20px;
}

.ant-collapse>.ant-collapse-item>.ant-collapse-header .arrow:before {
    content: "\25b8";
}

.ant-collapse>.ant-collapse-item {
    border: 0;
}

/* Form */
.ant-input {
    font-size: 13px;
}

.ant-form-item-label {
    text-align: left;
}

.ant-form-item {
    margin-bottom: 8px;
}

/* Layout helper elements */
.v-divider {
    display: inline-block;
    border-left: 1px solid rgba(151, 151, 151, 0.55);
    margin: 0 10px;
    height: 28px;
}

.h-divider {
    border-bottom: 1px solid rgba(151, 151, 151, 0.55);
    margin-bottom: 4px;
    height: 1em;
    width: 100%;
}

.ml-auto {
    float: right;
}

.border-right {
    border-right: 1px solid rgba(151, 151, 151, 0.55);
}

/* Search toolbar */
.search-toolbar {
    background-color: #ffffff;
    padding: 8px 8px 4px 8px;
    height: 55px;
}

.search-toolbar-title {
    display: block;
    font-size: 14px;
    color: #444A50;
    float: right;
}

.selected-element {
    display: inline-block;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 180px;
    line-height: 24px;
    height: 24px;
}

.selected-element-icon {
    padding: 0 5px 0 0;
}

.search-container {
    float: right;
}

.search-input.ant-input {
    width: 200px;
    margin: 0 15px;
    background: #FFFFFF;
    border: 1px solid rgba(210, 210, 210, 1.00);
    border-radius: 20px;
}

.switch-bar {
    text-align: right;
    padding-right: 22px;
}

.switch-bar .ant-switch {
    min-width: 35px;
    background-color: #cecece;
}

.switch-bar .ant-switch-checked {
    background-color: #1890ff;
}

.switch-bar .ant-switch:focus {
    outline: 3px solid #1890ff;
    outline-offset: 3px;
    box-shadow: none;
}

.switch-label {
    padding: 0 5px;
}

/* Selector */
.selector-title {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 10px;
    color: #444649;
    margin-bottom: 5px;
}

.ant-list.ant-list-selector,
.ant-tree,
.empty-list {
    background: #FFFFFF;
    border: 1px solid #DADADA;
    font-size: 13px;
    height: calc(100vh - 260px);
    min-height: 175px;
    overflow: auto;
}

.ant-list-bordered {
    border-radius: 0;
}

.ant-list-right {
    border-left: none;
}

.ant-list-bordered .ant-list-item {
    border: none;
    line-height: 30px;
    padding: 0 0 0 15px;
}

.ant-list-bordered .ant-list-item:nth-of-type(1) {
    padding-top: 4px;
}

.ant-list-item .ant-checkbox-wrapper {
    font-size: 12px;
    color: #444649;
}

.loading-indicator {
    height: 100%;
}

.ant-spin-nested-loading>div>.ant-spin {
    max-height: 448px;
}

.ant-checkbox-wrapper,
span.ant-checkbox {
    font-size: 13px;
}

.ant-checkbox-wrapper {
    white-space: nowrap;
}

.ant-checkbox-inner {
    width: 13px;
    height: 13px;
}

.ant-checkbox-inner:after {
    left: 2.9px;
    top: 0;
    width: 5px;
    height: 8px;
}

.ant-checkbox-group {
    display: block;
}

/* Tree */
.ant-tree {
    font-size: 12px;
}

.ant-tree li {
    padding-top: 0;
    padding-bottom: 0;
}

.ant-tree-iconEle {
    font-weight: bold;
    vertical-align: middle;
}

.folder-tree-container {
    max-height: 448px;
    height: 448px;
    overflow: auto;
}

.filter-list {
    background: #FFFFFF;
    border: 1px solid #DADADA;
    border-right: none;
    font-size: 13px;
    min-height: 175px;
    height: calc(100vh - 275px);
    color: #444649;
}

.filter-list .ant-tree {
    font-size: 14px;
    color: #444649;
    border: none;
    height: calc(100vh - 277px);
}

.filter-list .ant-tree-switcher.ant-tree-switcher-noop {
    display: none;
}

.filter-list .filter-checkbox {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding-left: 11px;
    vertical-align: middle;
}

.filter-list .filter-checkbox:before {
    margin-left: -7px;
}

/* Selector buttons */
.ant-btn {
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 4px;
    color: #444649;
}

.ant-btn:hover {
    background: #F3F3F3;
    border: 1px solid #D2D2D2;
    border-radius: 4px;
    color: #444649;
}

.ant-btn:focus {
    border: 1px solid #D2D2D2;
    box-shadow: #1C91DC 0 0 8px;
    border-radius: 4px;
    color: #444649;
}

.ant-btn.ant-btn-primary {
    color: #ffffff;
    background: #1C91DC;
    border: 1px solid #1C91DC;
    border-radius: 4px;
}

.ant-btn.ant-btn-primary:hover {
    background: #1384CC;
    border: 1px solid #1384CC;
    border-radius: 4px;
    color: #ffffff;
}

.ant-btn.ant-btn-primary[disabled] {
    background: #f5f5f5;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.25);
}

.selector-button.ant-btn {
    border: none;
    margin: 0;
    padding-right: 0;
    padding-left: 10px;
    height: auto;
    font-size: 10px;
    line-height: 1;
    color: #1890ff;
}

.preview-button {
    border: none;
    margin-top: 10px;
    height: auto;
    color: #1890ff;
    z-index: 1;
}

.preview-button .anticon,
.preview-button .anticon:hover {
    color: #979797;
}

/* Preview modal */
.ant-modal-mask {
    background-color: rgba(255, 255, 255, 0.7);
}

.ant-modal-content {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(151, 151, 151, 0.55);
}

.ant-modal-header {
    background-color: #ededed;
    padding: 8px 12px;
}

.ant-modal-title {
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
}

.ant-modal-close-x {
    width: 36px;
    height: 36px;
    line-height: 36px;
    font-weight: 400;
}

.ant-modal-body .ant-table-body {
    font-size: 12px;
    color: #444649;
    line-height: 30px;
}

/* Table */
.ant-table {
    font-size: 11px;
    font-weight: bolder;
}

.ant-table td {
    white-space: nowrap;
}

.ant-table-body {
    overflow-x: auto !important;
}

.preview-note {
    padding: 5px 0;
    font-size: 11px;
    text-align: right;
}

/* Icons */
.mstr-icon svg {
    max-width: 18px;
    max-height: 18px;
    vertical-align: text-top;
}

.mstr-icon.selector svg {
    max-height: 14px;
    vertical-align: sub;
    margin-right: 5px;
}

.filter-list .mstr-icon.selector svg {
    max-height: 20px;
    vertical-align: middle;
    margin-left: 5px;
}

.filter-list .ant-tree li span.ant-tree-iconEle {
    vertical-align: sub;
}

.mstr-icon.dataset svg {
    max-width: 14px;
    vertical-align: text-bottom;
}

.mstr-icon.certified svg {
    margin-left: 15px;
}

.mstr-icon.no-data svg {
    max-width: 64px;
    max-height: 41px;
}

.mstr-icon.hidden {
    margin-left: 5px;
}

/* Modal and Preview Data */
.modal-preview .ant-modal-header {
    background-color: #FFFFFF;
    padding: 12px;
}

.modal-preview .ant-modal-body {
    padding: 24px 0 15px 0;
}

.modal-preview .ant-modal-body>div>div {
    padding: 0 24px;
}

.modal-preview .ant-modal-header .ant-modal-title {
    font-size: 14px;
    font-weight: 400;
}

#mstr-table.ant-table-small>.ant-table-content>.ant-table-body>table {
    padding: 0;
}

#mstr-table.ant-table-small>.ant-table-content>.ant-table-scroll>.ant-table-body>table>.ant-table-tbody>tr>td,
#mstr-table.ant-table-small>.ant-table-content>.ant-table-scroll>.ant-table-body>table>.ant-table-thead>tr>th {
    padding: 5px 18px;
}

#mstr-table .ant-table-thead>tr>th {
    background-color: #FFFFFF;
    overflow: hidden;
    white-space: nowrap;
    font-size: 12px;
    font-weight: bold;
}

#mstr-table .ant-table-tbody>tr>td {
    font-size: 12px;
    font-weight: normal;
}

.preview-note {
    font-size: 10px;
    color: #9A9A9A;
    letter-spacing: 0.06px;
    line-height: 26px;
    padding-top: 0;
}

#close-preview-section {
    text-align: right;
    border-top: 1px solid #E8EDF0;
}

#close-preview-section .ant-radio-button-wrapper {
    margin-top: 15px;
}

.preview-info-icon {
    vertical-align: middle;
}

.preview-note span:nth-of-type(2) {
    vertical-align: sub;
}

td.metric-column {
    text-align: right;
}

th.metric-column {
    text-align: center !important;
}

th.attribute-column {
    text-align: center !important;
}

.ant-popover .ant-popover-inner-content {
    padding: 8px;
    background-color: #404040;
    color: white;
    border-radius: 4px;
    font-size: 12px;
    overflow-wrap: break-word;
}

.ant-popover.ant-popover-placement-bottom>.ant-popover-content>.ant-popover-arrow,
.ant-popover.ant-popover-placement-bottomRight>.ant-popover-content>.ant-popover-arrow,
.ant-popover.ant-popover-placement-bottomLeft>.ant-popover-content>.ant-popover-arrow {
    border-color: #404040;
}