.project-chooser {
    overflow-y: scroll;
    padding: 0 15px 0 15px;
}

.project-chooser table {
    width: 100%;
    padding: 10px;
}

.project-chooser .ant-tree {
    min-height: 0;
    height: 100%;
    border: 0;
}

.project-chooser .tree-title {
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    justify-content: space-between;
    color: #777777;
    font-size: 11px;
    padding: 5px;
    margin-bottom: 5px;
}

.project-chooser ul li span.ant-tree-node-content-wrapper {
    font-size: 14px;
    line-height: 14px;
    height: inherit;
    padding-left: 0;
}

.project-chooser .ant-tree li span.ant-tree-switcher.ant-tree-switcher_open:after {
    content: none;
}

.project-chooser .tree-container .ant-tree li {
    padding-top: 6px;
}

.project-chooser .ant-tree-checkbox-inner {
    width: 14px;
    height: 14px;
}

.project-chooser .ant-tree li span.ant-tree-iconEle {
    width: 16px;
    height: 16px;
    line-height: 16px;
    position: relative;
    top: -3px;
    margin-right: 3px;
}

.ant-tree-checkbox-inner:focus {
    outline: 3px solid #1890ff;
    outline-offset: 3px;
    box-shadow: none;
}

.ant-tree-checkbox-checked .ant-tree-checkbox-inner:after {
    width: 6px;
    height: 9px;
    left: 2px;
    top: 6px;
}

.project-chooser .ant-tree li span.ant-tree-checkbox {
    vertical-align: initial;
    margin: 0 5px 10px 0;
    padding: 0;
    height: 14px;
}

.project-chooser .ant-tree li span.ant-tree-switcher {
    line-height: 12px;
}

/* Remove buttons */
.project-chooser .remove-buttons {
    list-style: none;
    float: right;
    margin-top: 39px;
    margin-bottom:0;
    padding-left: 3px;
}

.project-chooser .remove-buttons li:not(:first-child) {
    padding-top: 15px;
}

.project-chooser .remove-buttons li {
    font-size: 13.5px;
    color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
}

.project-chooser .remove-buttons li:hover {
    color: #000;
}

/* Remove extra indentation for applications */
.project-chooser .ant-tree-child-tree-open .ant-tree-treenode-switcher-open .ant-tree-switcher-noop {
    width: 6px;
}

/* Header */
.project-chooser-header {
    display: flex;
    justify-content: space-between;
    margin: 10px 0 10px 0;
}

.project-chooser-header h2 {
    font-size: 14px;
    margin-left: 5px;
}

.project-chooser-header  .project-chooser-search {
    width: 220px;
    height: 26px;
}

.project-chooser-search input {
    border-radius: 13px;
    height: 26px;
}

.project-chooser-header div i {
    font-size: 16px;
    margin-right: 10px;
    cursor: pointer;
    color: #777;
}

.project-chooser-header div button {
    appearance: none;
    border: none;
    padding: 0;
    width: 26px;
    background: none;
}

.project-chooser-header div button:focus {
    outline-offset: 5px;
    z-index: 100;
}

.project-chooser-header div i:hover {
    color: #2F84CC;
}

.application-list-loading {
    height: calc(100vh - 100px)
}